.activity-add .tab-pane,
.activity-edit .tab-pane {
  padding-top: 10px;
}
.activity-add .form-control,
.activity-edit .form-control {
  max-width: 300px;
}
.activity-add label,
.activity-edit label {
  padding-top: 4px;
}
.activity-list table caption {
  font-weight: bold;
  font-size: 16px;
  text-align: center;
}
.activity-list table th {
  text-align: center;
}
.activity-list .table td {
  vertical-align: middle;
  text-align: center;
}
.activity-promotion-edit,
.activity-term-edit {
  border-top: none;
}
.activity-promotion-edit input,
.activity-term-edit input {
  max-width: 150px;
}
.activity-promotion-edit textarea,
.activity-term-edit textarea {
  max-width: 200px;
  resize: vertical;
}
.activity-promotion-edit th,
.activity-term-edit th,
.activity-promotion-edit td,
.activity-term-edit td {
  text-align: center;
}
.promotion-list table caption {
  font-weight: bold;
  font-size: 16px;
  text-align: center;
}
.promotion-list table th {
  text-align: center;
}
.promotion-list .table td {
  vertical-align: middle;
  text-align: center;
}
.coupon-list table caption {
  font-weight: bold;
  font-size: 16px;
  text-align: center;
}
.coupon-list table th {
  text-align: center;
}
.coupon-list .table td {
  vertical-align: middle;
  text-align: center;
}
.promotion-form-wrap .form-control {
  max-width: 300px;
}
.coupon-form-wrap .form-control {
  max-width: 300px;
}
